
import AbstractArea from '../../../AbstractArea'
import Area from '../../../Area';

export default class BBComponent extends AbstractArea {
   
    
    fetchUrlData(url) {

        if (url==null) return;
        var url2=url;
        if  (!url.includes("/api/projects/")) 
            {
                      //make it into an api call
                      if (url.includes("/projects/"))
                      {
                        url2=url.replace("/projects/","/api/projects/")
                        let lastPart = url.split("/").pop();
                        url2=url2.replace("/"+lastPart,"/viewers/"+lastPart)
                      }
           }
        //http://localhost:3000/api/projects/demo/viewers/f5mlE
        //http://localhost:3000/projects/primereact/f5mlE

      //  console.log(url2);
        try {
        fetch(url2)
            .then(response => response.json())
            .then(data => 
                {
                   // console.log(data.mainArea);
                   // var json = data.mainArea;
                    this.setState({ urlData: data.mainArea })

    });
}
catch(e)
{
    console.log(e);
}

    }

    constructor(props) {
        super(props);
        this.state = {
            url: props.url,
            urlData: props.urlData
        };
       
    }
    componentDidMount() {
        this.fetchUrlData(this.state.url);
    }
    componentDidUpdate(prevProps) {
        if (this.props.url !== prevProps.url) {
            this.fetchUrlData(this.props.url);
        }
    }   

    render() {
        var index =1;
        if (this.props.mode==="edit")
        {
            return (
                <div style={{ height:"100%", border: "2px solid red", padding: "10px" }} >
            <Area 
                               {...this.props}
                               disableFlexbox={false}
                               index={index}
                               config={this.state.urlData}
                               onChange={this.props.onChange}
                               >
            </Area>
            </div>

           )
        }
      

        return (
             
               <Area
                               {...this.props}
                               disableFlexbox={false}
                               index={index}
                               config={this.state.urlData}
                               onChange={this.props.onChange}
                               >
            </Area>
        )
        };
}